import React from 'react';
import "./password-input.css"
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import { IconButton } from '@mui/material';
import Tooltip from '@mui/material/Tooltip';

class PasswordInput extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        showPassword: false
      };
    }
  
    toggleShowPassword = () => {
      this.setState((prevState) => ({
        showPassword: !prevState.showPassword
      }));
    };
  
    render() {
      const { value, onChange } = this.props;
      const { showPassword } = this.state;
  
      return (
        <div className='password-input-div'>
            <IconButton  onClick={this.toggleShowPassword}>
                {showPassword ? <VisibilityOffIcon style={{color: 'white', height: '2.5vh', minHeight: '15px'}}></VisibilityOffIcon> : <VisibilityIcon style={{color: 'white', height: '2.5vh', minHeight: '15px'}}></VisibilityIcon>}
            </IconButton>
            {this.props.includeToolTip ? 
                <Tooltip placement='top'  title='A password must be at least 8 characters long, and contain one special character, and one letter.'>
                        <input
                            className="password-input"
                            type={showPassword ? "text" : "password"}
                            value={value}
                            onChange={(e) => onChange(e)}
                        />
                </Tooltip> 
                : 
                <input
                    className="password-input"
                    type={showPassword ? "text" : "password"}
                    value={value}
                    onChange={(e) => onChange(e)}
                />
            }


        </div>
      );
    }
}


export default PasswordInput;